import React, { useEffect, useState } from "react";
import axios from "axios";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Button from "@material-ui/core/Button";
import CreateIcon from "@material-ui/icons/Create";
import DeleteIcon from "@material-ui/icons/Delete";
import AddCircleOutlineIcon from "@material-ui/icons/AddCircleOutline";
import Typography from "@material-ui/core/Typography";

import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";
import { makeStyles } from "@material-ui/core/styles";

import ApiUrl from "../../../ServerApi";

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    "& > * + *": {
      marginTop: theme.spacing(2),
    },
  },
}));

const style = {
  marginTop: "25px",
  display: "flex",
  justifyContent: "center",
  textDecoration: "underline",
};

const Runz = (props) => {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [data, setData] = useState([]);
  const [message, setMessage] = useState(null);
  const [pageNumber, setPageNumber] = useState(0);
  const [totalPages, setTotalPages] = useState(null);

  const pagesBtn = new Array(totalPages).fill(null).map((v, i) => i);

  useEffect(() => {
    axios.get(`${ApiUrl}/procedures?page=${pageNumber}`).then((res) => {
      const { totalPage, contents } = res.data;
      setData(contents);
      setTotalPages(totalPage);
    });
  }, [pageNumber]);
  console.log(data);

  //
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  const addProc = () => {
    window.localStorage.clear();
    props.history.push("/addProce");
  };

  const deleteProc = async (userId) => {
    const deleteContent = data.find((user) => user._id === userId);
    axios.delete(`${ApiUrl}/procedures/${deleteContent._id}`);
    setData((prevContent) =>
      prevContent.filter((ele) => ele._id !== deleteContent._id)
    );
    setMessage("Template deleted successfully.");
    setOpen(true);
  };

  const editProc = (id) => {
    window.localStorage.clear();
    window.localStorage.setItem("userId", id);
    props.history.push("/editProce");
  };

  return (
    <div className={classes.root}>
      <Typography variant="h4" style={style}>
        Procedure Template Entries
      </Typography>
      <Button
        style={{
          border: "none",
          position: "absolute",
          top: "10px",
          right: "10px",
        }}
        onClick={() => addProc()}
      >
        <AddCircleOutlineIcon style={{ fontSize: "3.5em" }} />
      </Button>

      <Table>
        <TableHead>
          <TableRow>
            <TableCell>TemplateId</TableCell>
            <TableCell>Procedure Name</TableCell>
            <TableCell align="right">Action</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {data &&
            data.map((row) => (
              <TableRow key={row._id}>
                <TableCell component="th" scope="row">
                  {row._id.slice(row._id.length - 12)}
                </TableCell>
                <TableCell align="left">{row.title}</TableCell>
                <TableCell align="right" onClick={() => editProc(row._id)}>
                  <CreateIcon titleAccess="Edit" />
                </TableCell>
                {/* <TableCell align="right" onClick={() => deleteProc(row._id)}>
                  <DeleteIcon titleAccess="Delete" />
                </TableCell> */}
              </TableRow>
            ))}
        </TableBody>
      </Table>
      <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="success">
          {message}
        </Alert>
      </Snackbar>
      <h5>On page {pageNumber + 1}</h5>
      {pagesBtn.map((index) => (
        <button key={index} onClick={() => setPageNumber((prevState) => index)}>
          {index}
        </button>
      ))}
    </div>
  );
};

export default Runz;